.about_section {
    height: 50rem;
    background-image: url("../../assets//about_bg.webp");
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    overflow-x: hidden;
}

.about_data_container {
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: -50px;
}

.about_data_box {
    width: 28rem;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 20px;

    &>h2 {
        font-family: var(--f-libre-baskerville);
        text-transform: uppercase;
    }

    &>p {
        font-family: var(--f-inter);
        line-height: 1.5;
    }

    &>h3 {
        font-family: var(--f-merriweather);
        font-size: 1.5rem;
        text-transform: uppercase;
    }

    &>div {
        display: flex;
        gap: 10px;
        width: 21.5rem;

        &>p {
            font-family: var(--f-inter);
            line-height: 1.5;
        }

        &>span {


            &>img {
                width: 25px;
                object-fit: cover;
            }
        }
    }
}

@media (max-width: 85.62rem) {
    .about_section {
        height: auto;
        padding: 50px 0;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        & > img {
            width: 95%;
        }
    }

    .about_data_box {
        width: 95%;
    }
}