.doctors_section {
    height: 70vh;
    background-image: url("../../assets/bg-doctos.png");
    background-size: cover;
    background-position: center;
    display: grid;
    place-items: center;
    position: relative;
}

.doctors_container {
    display: flex;
    width: 100%;
    height: 100%;
    overflow: hidden;
    justify-content: center; /* Centraliza o conteúdo inicialmente */
}

.item_container_doctors {
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 100%;
    height: 100%;
}

.data_doc {
    height: 100%;
    width: 500px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 20px;

    & > .name {
        font-family: var(--f-alex-brush);
        font-size: 3.5rem;

    }

    & > .especiality {
        font-family: var(--f-inter);
    }

    & > .descrip {
        font-family: var(--f-inter);
        
    }

}

.btn_container {
    position: absolute;
    bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;

    & > button {
        background-color: transparent;
        border: none;
        height: 30px;
        width: 30px;
        color: var(--c-orange-heavy);
        cursor: pointer;
        & > svg {
            width: 100%;
            height: 100%;
        }
    }
}

.slider_wrapper {
    display: flex;
    justify-content: flex-start;
    transition: transform 0.3s ease-in-out;
}

.indicators_container {
    display: flex;
    justify-content: center;
    gap: 8px;
}

.indicator {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: var(--c-orange-light);
    border: none;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.indicator.active {
    background-color: var(--c-orange-heavy); /* Cor ativa */
}

@media (max-width: 85.62rem) {

    .doctors_section {
        height: auto;
        padding: 50px 0 70px 0;
    }

    .data_doc {
        width: 350px;
    }

    .item_container_doctors {
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
        width: 95%;
        gap: 30px;
        text-align: center;
        & > img {
            width: 300px;
        }

        & > div {
            width: 350px;
            display: grid;
            place-items: center;
        }
    }
    .especiality {
        width: 100%;
        text-align: center;
    }
}